import { Pagination, Table } from 'antd'
import React from 'react'
import { styled } from 'styled-components'
import UpdateTableBtn from './Actions/UpdateTableBtn'
import Search from './Actions/Search'
import DownloadList from './Actions/DownloadList'

export default function UsersTable({
    data,
    columns,
    query,
    setQuery,
    handlePageChange,
    updateTable,
    loading,
    handleDonwloadList,
    handleSearch
}) {


    return (
        <SUsersTable>
            <TableButtons>
                <UpdateTableBtn updateTable={updateTable} />
                <DownloadList handleDownload={handleDonwloadList} />
                <Search
                    handleSubmit={handleSearch}
                    query={query}
                    setQuery={setQuery}
                />
            </TableButtons >
            <STable
                dataSource={data.list}
                columns={columns}
                loading={loading}
                pagination={false}
            />
            <Pagination
                style={{ alignSelf: "flex-end" }}
                current={data.page}
                total={data.listCount}
                pageSize={10}
                onChange={handlePageChange}
            />
        </SUsersTable>
    )
}

const TableButtons = styled.div`
    width: 100%;
    display:flex;
    gap: 12px;
`
const SUsersTable = styled.div`
    width: 100%;
    display:flex;
    flex-direction:column;
    gap: 20px;
`
const STable = styled(Table)`
    width: 100%;
`
