import API from "../../API/API"
import { DELETE_CONSULT, SET_CONSULT_ERROR, SET_CONSULTS, SET_CONSULT_LOADING_FALSE, SET_CONSULT_LOADING_TRUE } from "../types"

const initialState = {
    consults: {
        list: []
    },
    loading: false,
    error: {}
}

const consultReducer = (state = initialState, action) => {
    switch(action.type) {
        case SET_CONSULTS:
            return {...state, consults: action.payload}
        case DELETE_CONSULT:
            return {...state, consults: {...state.consults, list: [...state.consults.list].filter(item => item.id !== action.payload)}}
        case SET_CONSULT_LOADING_TRUE:
            return {...state, loading: true}
        case SET_CONSULT_LOADING_FALSE:
            return {...state, loading: false}
        case SET_CONSULT_ERROR:
            return {...state, error: action.payload}
        default:
            return state
    }
}

export class ConsultActionCreators {
    static deleteConsult = consultId => async dispatch => {
        dispatch(this.setLoadingTrue())
        
        const response = await API.deleteConsult(consultId)
        if(response.statusText === "OK") {  
            dispatch({type: DELETE_CONSULT, payload: consultId})
        }else {
            dispatch(this.setError(response))
        }

        dispatch(this.setLoadingFalse())
    }

    static getConsults = params => async dispatch => {
        dispatch(this.setLoadingTrue())

        const response = await API.getConsults(params)
        if(response.statusText === "OK") {  
            dispatch({type: SET_CONSULTS, payload: response.data})
        }else {
            dispatch(this.setError(response))
        }

        dispatch(this.setLoadingFalse())
    }

    static setLoadingTrue = () => ({type: SET_CONSULT_LOADING_TRUE})
    static setLoadingFalse = () => ({type: SET_CONSULT_LOADING_FALSE})
    static setError = payload => ({type: SET_CONSULT_ERROR, payload})
}



export default consultReducer