import { Button, Modal } from 'antd'
import React, { useState } from 'react'
import { useDispatch } from 'react-redux'
import { styled } from 'styled-components'

export default function DeleteUserBtn({user, handleUserDelete}) {
    const dispatch = useDispatch()
    const [isModalOpen, setIsModalOpen] = useState(false)

    const showModal = () => {
        setIsModalOpen(true)
    }

    const handleOk = () => {
        setIsModalOpen(false)
    }

    const handleCancel = () => {
        setIsModalOpen(false)
    }


    return (
        <>
            <Button type="primary" onClick={showModal}>
                
            </Button>
            <SModal
                width="400px"
                open={isModalOpen}
                onOk={handleOk}
                onCancel={handleCancel}
                footer={false}
            >
                <Wrapper>
                    <h2>     {user.email}</h2>
                    <Buttons>
                        <Button style={{width: "100%"}} onClick={handleCancel}></Button>
                        <Button style={{width: "100%"}} onClick={() => {
                            dispatch(handleUserDelete())
                            handleCancel()
                        }}></Button>
                    </Buttons>
                </Wrapper>
            </SModal>
        </>
    )
}

const Buttons = styled.div`
    width: 100%;
    display:flex;
    gap: 20px;
`
const Wrapper = styled.div`
width: 100%;
    display:flex;
    flex-direction:column;
    gap: 20px;
`
const SModal = styled(Modal)`
    text-align:center;
`

