import API from "../../API/API"
import { DELETE_SALE, SET_SALE_ERROR, SET_SALES, SET_SALE_LOADING_FALSE, SET_SALE_LOADING_TRUE } from "../types"

const initialState = {
    sales: {
        list: []
    },
    loading: false,
    error: {}
}

const salesReducer = (state = initialState, action) => {
    switch(action.type) {
        case SET_SALES:
            return {...state, sales: action.payload}
        case DELETE_SALE:
            return {...state, sales: {...state.sales, list: [...state.sales.list].filter(item => item.id !== action.payload)}}
        case SET_SALE_LOADING_TRUE:
            return {...state, loading: true}
        case SET_SALE_LOADING_FALSE:
            return {...state, loading: false}
        case SET_SALE_ERROR:
            return {...state, error: action.payload}
        default:
            return state
    }
}

export class SalesActionCreators {
    static deleteSale = saleId => async dispatch => {
        dispatch(this.setLoadingTrue())

        const response = await API.deleteSale(saleId)
        if(response.statusText === "OK") {  
            dispatch({type: DELETE_SALE, payload: saleId})
        }else {
            dispatch(this.setError(response))
        }

        dispatch(this.setLoadingFalse())
    }

    static getSales = params => async dispatch => {
        dispatch(this.setLoadingTrue())

        const response = await API.getSales(params)
        if(response.statusText === "OK") {  
            dispatch({type: SET_SALES, payload: response.data})
        }else {
            dispatch(this.setError(response))
        }

        dispatch(this.setLoadingFalse())
    }

    static setLoadingTrue = () => ({type: SET_SALE_LOADING_TRUE})
    static setLoadingFalse = () => ({type: SET_SALE_LOADING_FALSE})
    static setError = payload => ({type: SET_SALE_ERROR, payload})
}



export default salesReducer