import ApiError from "../Exeptions/exeptions"
import axios from 'axios'

export const ENDPOINT = "http://api.amzpro.academy/api"

class API {
    async signIn(body) {
        try {
            const response = await axios.post(`${ENDPOINT}/admin/signIn`, body)
            return response
        }catch(e) {
            return ApiError.BadRequest({status: e.response.data.status, message: e.response.data.message})
        }
    }

    async getSales(params) {
        try {
            const response = await axios(`${ENDPOINT}/sales`, {params})
            return response
        }catch(e) {
            return ApiError.BadRequest({status: e.response.data.status, message: e.response.data.message})
        }
    }
    
    async deleteSale(saleId) {
        try {
            const response = await axios.delete(`${ENDPOINT}/sales/delete/${saleId}`)
            return response
        }catch(e) {
            return ApiError.BadRequest({status: e.response.data.status, message: e.response.data.message})
        }
    }


    async getConsults(params) {
        try {
            const response = await axios(`${ENDPOINT}/consults`, {params})
            return response
        }catch(e) {
            return ApiError.BadRequest({status: e.response.data.status, message: e.response.data.message})
        }
    }
    
    async deleteConsult(consultId) {
        try {
            const response = await axios.delete(`${ENDPOINT}/consults/delete/${consultId}`)
            return response
        }catch(e) {
            return ApiError.BadRequest({status: e.response.data.status, message: e.response.data.message})
        }
    }
}   

export default new API()