import React, { useEffect, useState } from 'react'
import UsersTable from '../UsersTable/UsersTable'
import { useDispatch, useSelector } from 'react-redux'
import { ConsultActionCreators } from '../../store/reducers/consultReducer'
import consultsColumns from './columns/consultsColumns'
import axios from 'axios'
import { ENDPOINT } from '../../API/API'

export default function ConsultTable() {
    const dispatch = useDispatch()
    const consults = useSelector(state => state.consult.consults)
    const loading = useSelector(state => state.consult.loading)
    const [query, setQuery] = useState("")

    useEffect(() => {
        dispatch(ConsultActionCreators.getConsults())
    }, [])

    const handlePageChange = page => {
        dispatch(ConsultActionCreators.getConsults({ page, search: query }))
    }

    const updateTable = () => {
        dispatch(ConsultActionCreators.getConsults())
        setQuery("")
    }

    const handleSearch = e => {
        e.preventDefault()
        dispatch(ConsultActionCreators.getConsults({ search: query }))
    }

    const handleConsultDelete = user => {
        dispatch(ConsultActionCreators.deleteConsult(user.id))
    }

    const handleDonwloadList = async () => {
        const list = (await axios.get(`${ENDPOINT}/consults/get-all`)).data

        if (!list?.length) {
            return console.log("No data")
        }

        const csvRows = list.map(obj => Object.values(obj).join(','));
        const header = Object.keys(list[0]).join(',');
        const csvContent = `${header}\n${csvRows.join('\n')}`;

        const blob = new Blob([csvContent], { type: 'text/csv' });
        const url = URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.href = url;
        a.download = ' .csv';
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
        URL.revokeObjectURL(url);
    }

    return (
        <div>
            <UsersTable
                data={consults}
                columns={consultsColumns}
                query={query}
                setQuery={setQuery}
                handlePageChange={handlePageChange}
                updateTable={updateTable}
                handleSearch={handleSearch}
                handleUserDelete={handleConsultDelete}
                loading={loading}
                handleDonwloadList={handleDonwloadList}
            />
        </div>
    )
}
