import { Button, Input, message } from 'antd'
import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import { useDispatch, useSelector } from 'react-redux'
import { AuthActinonCreators } from '../../store/reducers/authReducer'
import Loader from '../../UI/Loader'

export default function Login() {
    const dispatch = useDispatch()
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const loading = useSelector(state => state.auth.loading)
    const error = useSelector(state => state.auth.error)
    const [messageApi, contextHolder] = message.useMessage()

    const handleSignIn = () => {
        if (email && password) {
            dispatch(AuthActinonCreators.signIn({ email, password }))
        }
    }

    useEffect(() => {
        if (error.message) {
            messageApi.open({
                type: "error",
                content: error.message
            })
        }
    }, [error.message])


    return (
        <SLogin>
            {contextHolder}
            <Form onSubmit={handleSignIn}>
                <h2 style={{ textAlign: "center" }}></h2>
                <Input value={email} type='email' placeholder='' onChange={e => setEmail(e.target.value)} />
                <Input value={password} type='password' placeholder='' onChange={e => setPassword(e.target.value)} />
                {error.message && <p style={{ color: "red", textAlign: "center" }}>{error.message}</p>}
                {loading && <Loader />}
                <Button onClick={handleSignIn}></Button>
            </Form>
        </SLogin>
    )
}


const SLogin = styled.div`
    width: 100%;
    display:flex;
    justify-content:center;
`
const Form = styled.form`
    width: 300px;
    padding: 20px;
    border: 1px solid #e3e3e3;
    border-radius: 5px;
    display:flex;
    flex-direction: column;
    justify-content:center;

    gap: 20px;

`
